import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable()
export class DataService {

  private messageSource = new BehaviorSubject('');
  private messageSources = new BehaviorSubject('');
  private messageSourcess = new BehaviorSubject('');
  private messageSourcesss = new BehaviorSubject('');
  private messageSourcessss = new BehaviorSubject('');

  currentMessage = this.messageSource.asObservable();
  currentMessages = this.messageSources.asObservable();
  currentMessagess = this.messageSourcess.asObservable();
  currentMessagesss = this.messageSourcesss.asObservable();
  currentMessagessss = this.messageSourcessss.asObservable();

  constructor() { }

  changeMessage(message) {
    this.messageSource.next(message)
  }

  changehead(msg){
    this.messageSources.next(msg)
  }

  changelatlng(msg){
    this.messageSourcess.next(msg)
  }

  changecat(msgg){
    this.messageSourcesss.next(msgg)
  }
  changecathead(msgg){
    this.messageSourcessss.next(msgg)
  }

}