  getLocation() {
    
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition((position: Position) => {
        if (position) {
          console.log(position);
          console.log("Latitude: " + position.coords.latitude +
            "Longitude: " + position.coords.longitude);
          this.lat = position.coords.latitude;
          this.lng = position.coords.longitude;
          this.getAddress(this.lat, this.lng);
          this.httpclient.post(this.serverUrl + 'placerecords', { distance: "20km", lat: this.lat, lng: this.lng, cat: [], place_id: '0' }).subscribe(

            datas => {
              this.loaderService.display(false);
              this.placeList = datas;
              this.markers = [];
              this.placeList.forEach((key: any, val: any) => {
                //console.log(key);
                this.markers.push({
                  lat: key.lat,
                  lng: key.lng,
                  label: key.name,
                  opening_hours: key.opening_hours,
                  rating: key.rating,
                  formatted_address: key.formatted_address,
                  website_address:key.website_address,
                  price:key.price,
                  img: key.photo[0].img
                });
              });
              this.mapReady();
            },

            error => {

              console.log("Rrror", error);

            }

          );
        }
      },
        (error: PositionError) => {
          this.httpclient.post(this.serverUrl + 'placerecords', { distance: "20km", lat: this.lat, lng: this.lng, cat: [], place_id: '0' }).subscribe(

            datas => {
              this.loaderService.display(false);
              this.placeList = datas;
              this.markers = [];
              this.placeList.forEach((key: any, val: any) => {
                //console.log(key);
                this.markers.push({
                  lat: key.lat,
                  lng: key.lng,
                  label: key.name,
                  opening_hours: key.opening_hours,
                  rating: key.rating,
                  formatted_address: key.formatted_address,
                  img: key.photo[0].img
                });
              });
            },

            error => {

              console.log("Rrror", error);

            }

          );
        }, { maximumAge: 0, timeout: 60000, enableHighAccuracy: true });
    } else {
      alert("Geolocation is not supported by this browser.");
    }
    
  }

